.verses{
    display: grid;
 text-align:left;
    padding: 20px 20px 20px 20px;
    margin-top: 0;
background-color: #F7F7F7;
cursor: pointer;
overflow: auto;

}
.surah{
    margin-left: 600px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
font-size: xxx-large;
}

.bis{
    text-align: center;
}
.reading{
    font-size: x-large;
}
.text{
font-size: x-large;
   font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
text-decoration:double;

color: black;

 
}
.bism{
    margin-left: 600px;
    margin-bottom: 20px;
    margin-top: -9px;
    font-size: xx-large;
}
.right{
    text-align: right;
    font-size: xx-large;
    font-family: 'Bebas Neue', cursive;
}
.right:hover{
    color: green;
}

.left{
    font-size: x-large;
    margin-top: 50px;
}
.left:hover{
    color: green;
}

@media only screen and (max-width: 1000px) {
    .verses{
justify-content: left;
        text-align: left;

        padding: 20px 20px 20px 20px;
        margin-top: 30px;
        background-color: #F7F7F7;
    }
    .surah{
margin-left: 70px;
text-align: center;
        font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: xxx-large;
    }
    .text{
        font-size:large;

        font-family: 'Times New Roman', Times, serif;
    }
    .bis{
        text-align: center;
    }
    .bism{
display: none;
        margin-bottom: 20px;
        margin-top: -9px;
    }
}


